body {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

#root {
  background-color: #f0f0f0;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 700px;
  padding: 20px;
  display: flex;
}

#pro {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 2rem;
  font-weight: bolder;
  text-decoration: line-through 1.2rem #a59f9f68;
}

#container {
  justify-content: space-between;
  align-items: center;
  width: 70%;
  margin-bottom: 20px;
  display: flex;
}

.item {
  align-items: center;
  font-weight: bold;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  transform: all 1s;
  background-color: #005cc8;
  border-radius: 10px;
  padding: 1rem;
  font-family: Arial, Helvetica, sans-serif;
  font-size: x-large;
  font-weight: bolder;
}

button:hover {
  color: #005cc8;
  background-color: #fff;
  padding: 1.1rem;
}

@media (width <= 600px) {
  #root {
    justify-content: space-evenly;
    width: 100%;
    height: 300px;
  }

  #container {
    flex-direction: column;
    height: 30%;
  }

  h1 {
    font-size: 1.3rem;
  }

  #pro {
    font-size: 1.3rem;
    text-decoration-thickness: 1rem;
  }
}
